<script>
import ErrorPopup from '@/components/ErrorPopup.vue';
export default {
	components: {
		ErrorPopup,
	},
	data() {
		return {
			companyInfo: {
				name: '삼성화재해상보험',
				date: '2019.07.25 10:25',
				monthData: 2,
      },

      // 통신사 선택 데이터
      sktImg:false,
      ktImg:false,
      lgImg:false,

			// 폰 넘버
			phoneNumber: '',

			//모두 동의
			agreeAll: false,

			//약관동의 (순서별로)
			// agree1: false,
			agree2: false,
			agree3: false,
			agree4: false,
			agree5: false,

			//에러팝업
			errorPopup: false,
		};
	},

	methods: {
    selectAgency(index){
      switch (index) {
				// SKT 선택시 KT,LG 체크해제
				case 1:
          this.sktImg = true;
          this.ktImg = false;
          this.lgImg = false;
					break;

				// KT 선택시 SKT,Lg 체크해제
        case 2:
          this.ktImg = true;
          this.lgImg = false;
					this.sktImg = false;
          break;
          
        // LG 선택시 SKT,KT 체크해제
        case 3:
        this.lgImg = true;
        this.sktImg = false;
        this.ktImg = false;
        break;
			}
    },

		// 에러팝업 (전화번호가 오류났을시 true로 변경 ErrorPopup에 props : errorShow로 데이터를 넘겨줌)
		errorShow() {
			this.errorPopup = true;
		},
		// (에러팝업 종료시 ErrorPopup에서 errorCancel 이벤트를 연결시켜 errorPopup를 종료시킴 )
		errorCancel() {
			this.errorPopup = false;
		},

		// topBtn 이벤트 발생시 스크롤 최상단으로 이동
		topBtn() {
			document.documentElement.scrollTop = 0;
		},

		// 이용약관 모두동의 버튼 클릭 이벤트
		agreeAllOn() {
			this.agreeAll = !this.agreeAll;
			if (this.agreeAll == true) {
				// this.agree1 = true;
				this.agree2 = true;
				this.agree3 = true;
				this.agree4 = true;
				this.agree5 = true;
			} else {
				// this.agree1 = false;
				this.agree2 = false;
				this.agree3 = false;
				this.agree4 = false;
				this.agree5 = false;
			}
		},

		// 이용약관 클릭 이벤트
		checkClick(index) {
			switch (index) {
				// case 1:
				// 	this.agree1 = !this.agree1;
				// 	break;
				case 2:
					this.agree2 = !this.agree2;
					break;
				case 3:
					this.agree3 = !this.agree3;
					break;
				case 4:
					this.agree4 = !this.agree4;
					break;
				case 5:
					this.agree5 = !this.agree5;
					break;
			}
			// 이용약관을 모두 체크하면 모두동의에 체크 && 이용약관 중 하나라도 체크가 안되면 모두동의 체크가 풀림
			if (this.agree2 & this.agree3 & this.agree4 & this.agree5) {
				this.agreeAll = true;
			} else {
				this.agreeAll = false;
			}
		},

		// 유료가입 버튼클릭시 checkForm이벤트 발생
		checkForm() {
			// 전화번호가 10자리이하일때 휴대폰번호 오류 팝업창이 띄워짐
			if (this.phoneNumber.length <= 10) {
				this.errorShow();
				return false;
			}

			// 전화번호 앞 3자리에 numberChk의 번호이외의 번호가 입력됐을때 휴대폰번호 오류 팝업창이 띄워짐
			let numberChk = this.phoneNumber.substring(0, 3);
			if (
				numberChk == '010' ||
				numberChk == '011' ||
				numberChk == '016' ||
				numberChk == '017' ||
				numberChk == '018' ||
				numberChk == '019'
			) {
				console.log('sucsess');
			} else {
				this.errorShow();
				return false;
			}

			// 필수약관을 동의하지 않았을때 경고문
			if (
				this.agree1 &&
				this.agree2 &&
				this.agree3 &&
				this.agree4 &&
				this.agree5
			) {
				console.log('sucsess');
			} else {
				alert('서비스 이용약관 등을 모두 동의하셔야 서비스 가입이 가능합니다.');
				return false;
			}

			// pass금융사기방지 유료가입 버튼 클릭시 certification 컴포넌트로 이동
			this.$router.replace('Certification2');
		},
	},
};

//스크롤이벤트
window.onscroll = function() {
	var sclV = window.scrollY;
	if (sclV > 10) {
		document.querySelector('.topBtn').classList.add('fixed');
	} else {
		document.querySelector('.topBtn').classList.remove('fixed');
	}
};
</script>

